import { useEffect, useRef } from "react";
import { select } from 'd3';
import { DrawEyesChart } from "./DrawEyesChart";

export const EyesChart = ({
  data,
  width,
  height,
  sliderLeft,
  sliderRight
}) => {
  const margin = 20;
  const gap = 10; //   
  const svgRef = useRef();

  useEffect(() => {
    const SVG = select(svgRef.current)

    const singleWidth = (width - gap) / 2;


  //    `sliderLeft`
  DrawEyesChart(
    SVG,
    singleWidth,
    "#50C3F9",
    height,
    margin,
    sliderLeft,
  );

  //    `sliderRight`
  DrawEyesChart(
    SVG,
    singleWidth,
    "#C88CF8",
    height,
    margin,
    sliderRight,
    singleWidth + gap //      
  );
  }, [data, height, width,sliderLeft, sliderRight])
  
  return <svg
          ref={svgRef}
          viewBox={`0 0 ${width} ${height}`}
         >
         </ svg>

}